import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { token } from './token';

@Injectable({
  providedIn: 'root'
})
export class RoomService {
  constructor(private _http: HttpClient) { }

  getToken(): Observable<any> {
  return this._http.get<any>('http://copocse.cet.ac.in/getToken');
  }

  getMatch(userToken): Observable<any> {
    let headers = new HttpHeaders({
      'token': userToken
    });
    let options = {
      headers: headers
    }

    return this._http.get<any>('http://copocse.cet.ac.in/match', options)
  }

  getMessages(userToken, chatToken): Observable<any> {
    let headers = new HttpHeaders({
      'token': userToken,
      'chatToken': chatToken
    });
    let options = {
      headers: headers
    }

    return this._http.get<any>('http://copocse.cet.ac.in/receive', options);
  }

  sendMessage(userToken, chatToken, msg) {
    let headers = new HttpHeaders({
      'token': userToken,
      'chatToken': chatToken,
    });
    let options = {
      headers: headers
    }
    
    this._http.post('http://copocse.cet.ac.in/send', {msg: msg}, options).toPromise().then(data => data);
  }
}
